body {
  margin: 0;
  background: url("./altitude.jpg");
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main-div{
  display: flex;
  justify-content: center;
  margin-top: 5%;
  
}
.child-div{
  text-align: center;
  padding-top: 0;
  padding-left: 5%;
  padding-right: 5%;
  padding-bottom: 4%;
  width: 80%;
  background-color: rgba(255, 255, 255, 0.22);
  box-shadow: 0 8px 32px 0 rgba(31,38,135,0.37);
  border-radius: 10px;
  backdrop-filter: blur(7px);
  -webkit-backdrop-filter: blur(5px);
}

.caption{
  color:white;
  letter-spacing: .1rem;
  margin-bottom: 5%;
  font-size: 1.2rem;
  font-weight: 700;
}
.inputField{
  width: 20vw;
  height: 3.2vh;
  font-size: .9rem;
  padding-left: 3%;
  border: 0;
}
.inputField,
.inputField:focus,
.inputField:active,
.inputField:active:focus{
  border: none;
  outline: none;
}

.inputField:focus{
  -webkit-box-shadow: 0 0 1.5rem rgba(0, 0, 0, 0.2);
  box-shadow: 0 0 1.5rem rgba(0, 0, 0, 0.2);
}
.addItems {
  background-color: white;
  width: 25%;
  height: 3.5vh;
  display: flex;
  margin-left: 37%;
  margin-right: 30%;
}
.addBtn{
  background: black;
  text-decoration: none;
  font-size: .9rem;
  border: 0;
  border-color: black;
  color: white;
  width: 5vw;
  height: 3.5vh;
  float: right;
  padding-top: .6vh;
  text-align: center;
}

.showItems{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.singleItem{
  display: flex;
  margin-top: 5vh;
  margin-left: 2%;
  margin-right: 2%;
  min-height: 14vh;
  width: 20vw;
  padding: 1%;
  font-size: .8rem;
  letter-spacing:.03rem;
  text-align: center;
  align-items: center;
  justify-content: space-between;
  background-color:#1adddd;
  border-radius: .5rem;
}
.singleItem:hover{
  background-color: white;
}
.todoBtn{
  cursor: pointer;
  display: flex;
  align-self: flex-end;
}

.removeAll{
  margin-left: 35%;
  margin-right: 35%;
  margin-top: 2vh;
  min-height: 3.5vh;
  font-size: .9rem;
  border: 0;
  color: white;
  background-color: rgb(0, 0, 0);
  border-radius: .2rem;
}
.removeAll:hover{
  background-color: rgb(116, 39, 39);
}

@media screen and (max-width:520px){
  .addItems{
    width: 90%;
    margin-left: 5%;
  }
    .inputField{
      width: 70vw;
    }
    .addBtn{
      width: 20vw;
    }
    .singleItem{
      display: flex;
      width: 90%;
      flex-direction: column;
    }
}